___CSS_LOADER_EXPORT___.push([module.id, `/* General Styles */
body {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
    background-size: cover;
    background-position: center;
    color: #333;
}

.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
}

/* Header Styles */
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
}

header h1 {
    margin: 0;
    font-size: 24px;
}

.search {
    display: flex;
    align-items: center;
}

.search input {
    padding: 5px;
    border: none;
    border-radius: 5px;
    margin-right: 10px;
    font-size: 16px;
}

.search button {
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    background-color: #0056b3;
    color: white;
    cursor: pointer;
    font-size: 16px;
}

.theme-toggle {
    background: none;
    border: none;
    color: white;
    font-size: 24px;
    cursor: pointer;
}

.theme-toggle:focus {
    outline: none;
}

/* Main Content Styles */
main {
    margin-top: 20px;
}

#current-weather,
#hourly-forecast,
#forecast,
#weather-alerts,
#weather-map,
#favorites,
#weather-news,
#detailed-metrics,
#clothing-suggestion,
#air-quality {
    margin-bottom: 20px;
    padding: 20px;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

#current-weather h2,
#hourly-forecast h2,
#forecast h2,
#weather-alerts h2,
#weather-news h2,
#detailed-metrics h2,
#clothing-suggestion h2,
#air-quality h2 {
    margin-top: 0;
}

/* Forecast Styles */
.forecast-day,
.hourly-forecast-hour {
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: center;
    margin-bottom: 10px;
    padding: 10px;
    background-color: #f9f9f9;
    border-radius: 5px;
    gap: 10px;
}

.forecast-day p,
.hourly-forecast-hour p {
    margin: 0;
}

/* Footer Styles */
footer {
    text-align: center;
    margin-top: 20px;
    color: #777;
}

/* Weather Icon Styles */
.weather-icon {
    font-size: 48px;
    margin-right: 10px;
}

/* Light and Dark Mode Styles */
body.light-mode {
    background-color: #ffffff;
    color: #333333;
}

body.dark-mode {
    background-color: #333333;
    color: #ffffff;
}

.dark-mode header {
    background-color: #444444;
}

.dark-mode .search button {
    background-color: #222222;
}

.dark-mode #current-weather,
.dark-mode #hourly-forecast,
.dark-mode #forecast,
.dark-mode #weather-alerts,
.dark-mode #weather-map,
.dark-mode #favorites,
.dark-mode #weather-news,
.dark-mode #detailed-metrics,
.dark-mode #clothing-suggestion,
.dark-mode #air-quality {
    background-color: #444444;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
}

.dark-mode .forecast-day,
.dark-mode .hourly-forecast-hour {
    background-color: #555555;
}

.dark-mode .forecast-day p,
.dark-mode .hourly-forecast-hour p,
.dark-mode #current-weather p,
.dark-mode #hourly-forecast h2,
.dark-mode #forecast h2,
.dark-mode #weather-alerts h2,
.dark-mode #weather-news h2,
.dark-mode #detailed-metrics h2,
.dark-mode #clothing-suggestion h2,
.dark-mode #air-quality h2 {
    color: #ffffff;
}`, "",{"version":3,"sources":["webpack://./src/css/styles.css"],"names":[],"mappings":"AAAA,mBAAmB;AACnB;IACI,iCAAiC;IACjC,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,2BAA2B;IAC3B,WAAW;AACf;;AAEA;IACI,gBAAgB;IAChB,cAAc;IACd,aAAa;AACjB;;AAEA,kBAAkB;AAClB;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,yBAAyB;IACzB,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,SAAS;IACT,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;IAClB,yBAAyB;IACzB,YAAY;IACZ,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,YAAY;IACZ,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,aAAa;AACjB;;AAEA,wBAAwB;AACxB;IACI,gBAAgB;AACpB;;AAEA;;;;;;;;;;IAUI,mBAAmB;IACnB,aAAa;IACb,uBAAuB;IACvB,kBAAkB;IAClB,uCAAuC;AAC3C;;AAEA;;;;;;;;IAQI,aAAa;AACjB;;AAEA,oBAAoB;AACpB;;IAEI,aAAa;IACb,+BAA+B;IAC/B,mBAAmB;IACnB,mBAAmB;IACnB,aAAa;IACb,yBAAyB;IACzB,kBAAkB;IAClB,SAAS;AACb;;AAEA;;IAEI,SAAS;AACb;;AAEA,kBAAkB;AAClB;IACI,kBAAkB;IAClB,gBAAgB;IAChB,WAAW;AACf;;AAEA,wBAAwB;AACxB;IACI,eAAe;IACf,kBAAkB;AACtB;;AAEA,+BAA+B;AAC/B;IACI,yBAAyB;IACzB,cAAc;AAClB;;AAEA;IACI,yBAAyB;IACzB,cAAc;AAClB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;;;;;;;;;;IAUI,yBAAyB;IACzB,6CAA6C;AACjD;;AAEA;;IAEI,yBAAyB;AAC7B;;AAEA;;;;;;;;;;IAUI,cAAc;AAClB","sourcesContent":["/* General Styles */\r\nbody {\r\n    font-family: 'Roboto', sans-serif;\r\n    margin: 0;\r\n    padding: 0;\r\n    background-size: cover;\r\n    background-position: center;\r\n    color: #333;\r\n}\r\n\r\n.container {\r\n    max-width: 800px;\r\n    margin: 0 auto;\r\n    padding: 20px;\r\n}\r\n\r\n/* Header Styles */\r\nheader {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    background-color: #007bff;\r\n    color: white;\r\n    padding: 10px 20px;\r\n    border-radius: 5px;\r\n}\r\n\r\nheader h1 {\r\n    margin: 0;\r\n    font-size: 24px;\r\n}\r\n\r\n.search {\r\n    display: flex;\r\n    align-items: center;\r\n}\r\n\r\n.search input {\r\n 